<template>
    <div class="home">
        <div class="header">
            控制台参数化设计平台
        </div>
        <div class="main">
          <div class="title1">输入初始条件:</div>
          <div class="main-left">
            <div class="screenSize">
               选择屏幕比例 <span class="hide">李</span>
              <span class="button-style"
                    :class="{activeBg: setSizeBg[0]}"
                    @click="setSize(0,0)">16:9</span>
              <span class="button-style"
                    :class="{activeBg: setSizeBg[1]}"
                    @click="setSize(1,1)">4:3</span>
            </div>
            <div class="screenNumber">
              选择屏幕分辨率<span class="hide">:</span>
              <span class="button-style"
                    :class="{activeBg: setBg[0]}"
                    @click="setB(1920,0)">1920*1080</span>
              <span class="button-style"
                    :class="{activeBg: setBg[1]}"
                    @click="setB(1024,1)">1024*768</span>
              <div class="one">
                <span class="hide">选择屏幕分辨率:</span>
                <span class="button-style"
                      :class="{activeBg: setBg[2]}"
                      @click="setB(1280,2)">1280*720</span>
                <span class="button-style"
                      :class="{activeBg: setBg[3]}"
                      @click="setB(800,3)">800*600</span>
              </div>
              <div class="one">
                <span class="hide">选择屏幕分辨率:</span>
                <span class="button-style hideNumber">16:9</span>
                <span class="button-style"
                      :class="{activeBg: setBg[4]}"
                      @click="setB(640,4)">640*480</span>
              </div>
            </div>
            <div class="screenCun">
              请输入屏幕尺寸 <span class="hide">:</span>
              <input type="number" v-model="x"> 英寸
            </div>
            <div class="manBody">
              请选择人体尺寸
              <span class="button-two"
                    :class="{activeBg: setSexBg[0]}"
                    @click="setSex(0,0)">男性</span>
              <span class="button-two"
                    :class="{activeBg: setSexBg[1]}"
                    @click="setSex(1,1)">女性</span>
            </div>
            <div class="manBody">
              <span class="hide">请选择人体尺寸</span>
              <span class="button-two"
                    :class="{activeBg: setBodySizeBg[0]}"
                    @click="setBodySize(0.05,0)">5%</span>
              <span class="button-two"
                    :class="{activeBg: setBodySizeBg[1]}"
                    @click="setBodySize(0.5,1)">50%</span>
              <span class="button-two"
                    :class="{activeBg: setBodySizeBg[2]}"
                    @click="setBodySize(0.95,2)">95%</span>
            </div>
            <div class="count" @click="totalCount()">计算</div>
          </div>
          <div class="title2">输出主要设计参数:</div>
          <div class="main-right">
              <div class="biao">
                <div class="number1">{{h7}}</div>
                <div class="number2">{{h8}}</div>
                <div class="number3">{{a1}}</div>
                <div class="number4">{{h6}}</div>
                <div class="number5">{{h5}}</div>
                <div class="number6">{{h4}}</div>
                <div class="number7">{{h3}}</div>

              </div>
          </div>

        </div>
      <div class="bottom">

      </div>
    </div>
</template>
<script>

    export default {
        data() {
            return {
              /*屏幕的分辨率*/
              b: 0,
              /*屏幕的尺寸*/
              x: 0,
              /*性别*/
              sex: 0,
              /*身体*/
              bodysize: 0,
              /*屏幕尺寸*/
               size: 0,
               h1: 0,
               h2: 0,
               L: 0,
               L1: 0,
               PI: Math.PI,
              a1: 0,
              h3: 0,
              h4: 0,
              h5: 0,
              h6: 0,
              h7: 0,
              h8: 0,
              /*处理点击变色*/
              setSizeBg: [false,false],
              setBg: [false,false,false,false,false],
              setSexBg: [false,false],
              setBodySizeBg: [false,false,false]
            }
        },
        props: [''],
        computed: {},
        methods: {
          initClass(arr, index) {
              for(let i =0; i < arr.length;i++) {
                if(i===index) {
                  arr.splice(i,1,true);
                } else {
                  arr.splice(i,1,false);
                }
              }
          },
          setB(val,index) {
            this.b = val;
            this.initClass(this.setBg, index);

          },
          setSex(val,index) {
            this.sex = val;
            this.initClass(this.setSexBg, index);
          },
          setBodySize(val,index) {
            this.bodysize = val;
            this.initClass(this.setBodySizeBg, index);
          },
          setSize(val,index) {
            this.size = val;
            this.initClass(this.setSizeBg, index);
          },
          /*计算*/
          totalCount() {
            let {sex,bodysize,h1,h2,h3,h4,h5,h6,h7,h8,L,L1,size,PI,a1,x,b} = this;
            if (sex === 0) {
              //男
              switch(bodysize)
              {
                case 0.95:
                  h1=1302;
                  h2=1094;
                  L=338;
                  L1=593;
                  break;
                case 0.5:
                  h1=1223;
                  h2=1021;
                  L=313;
                  L1=554;
                  break;
                case 0.05:
                  h1=1157;
                  h2=958;
                  L=289;
                  L1=515;
                  break;
              }

            }else{
              switch(bodysize)
              {
                case 0.95:
                  h1=1206;
                  h2=1013;
                  L=302;
                  L1=570;
                  break;
                case 0.5:
                  h1=1148;
                  h2=959;
                  L=284;
                  L1=529;
                  break;
                case 0.05:
                  h1=1100;
                  h2=910;
                  L=262;
                  L1=495;
                  break;
              }
            }

            if(size === 0){
              a1=91440*x/(2.04*b*PI);
              h3=(a1*Math.sin(2*PI/360*45)/Math.sin(2*PI/360*59))*Math.cos(2*PI/360*14)+h1;
              h4=(a1*Math.sin(2*PI/360*10)/Math.sin(2*PI/360*70))*Math.cos(2*PI/360*10)+h1;
              h5=h1-(a1*Math.sin(2*PI/360*30)/Math.sin(2*PI/360*70)*Math.cos(2*PI/360*10));
              h6=h2-(L*Math.cos(2*PI/360*23)*Math.cos(2*PI/360*10));
              h7=394;
              h8=L1;
            }else{
              a1=91440*x/(1.667*b*PI);
              h3=(a1*Math.sin(2*PI/360*45)/Math.sin(2*PI/360*59))*Math.cos(2*PI/360*14)+h1;
              h4=(a1*Math.sin(2*PI/360*10)/Math.sin(2*PI/360*70))*Math.cos(2*PI/360*10)+h1;
              h5=h1-(a1*Math.sin(2*PI/360*30)/Math.sin(2*PI/360*80)*Math.cos(2*PI/360*10));
              h6=h2-(L*Math.cos(2*PI/360*23)*Math.cos(2*PI/360*10));
              h7=394;
              h8=L1;
            }

            this.a1 = a1.toFixed(2);
            this.h3 = h3.toFixed(2);
            this.h4 = h4.toFixed(2);
            this.h5 = h5.toFixed(2);
            this.h6 = h6.toFixed(2);
            this.h7 = h7.toFixed(2);
            this.h8 = h8.toFixed(2);
          }

        },
        components: {}
    }
</script>

<style lang="less" scoped>
    .home {
      height: 100%;
      .header {
        height: 10%;
        color: #fff;
        font-size: 25px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: url("../../static/header.jpg") no-repeat;
        background-size: 100% 100%;
      }
      .main {
        height: 87%;
        display: flex;
        align-items: center;
        justify-content: space-around;
        position: relative;


        .activeBg {
          color: #fff;
          background: #8cca7a !important;
        }
        .title1,.title2 {
          color: #707070;
          position: absolute;
        }
        .title1 {
          left: 3%;
          top: 5%;
        }
        .title2 {
          left: 53%;
          top: 5%;
        }
        .main-left,.main-right {
          width: 45%;
          height: 80%;
          border: 1px solid #707070;
        }

        .main-left {
          padding-left: 2.5%;
          padding-top: 1%;
          box-sizing: border-box;
          font-size: 16px;
          .hide {
            color: transparent;
          }
          .button-style {
            display: inline-block;
            text-align: center;
            width: 30%;
            height: 30px;
            border-radius: 10px;
            line-height: 30px;
            margin-left: 15px;
            background: #eee;
          }
          .screenNumber,.screenCun {
            margin-top: 30px;
          }
          .one {
            margin-top: 15px;
            .hideNumber {
             opacity: 0;
            }
          }
          input {
            height: 25px;
            margin-right: 10px;
          }

          .manBody {
            margin-top: 30px;
            .button-two {
              display: inline-block;
              width: 20%;
              height: 30px;
              text-align: center;
              line-height: 30px;
              background: #eee;
              border-radius: 8px;
              margin-left: 10px;
            }
          }
          .count {
            margin-top: 40px;
            width: 125px;
            height: 30px;
            color: #fff;
            line-height: 30px;
            background: #50b371;
            border-radius: 10px;
            text-align: center;
          }
        }
        .main-right {
          display: flex;
          align-items: center;
          justify-content: center;
          .biao {
            width: 90%;
            height: 90%;
            background: url("../../static/biao.jpg") no-repeat;
            background-size: 100% 100%;
            position: relative;
            .number1,.number2,.number3,.number4,.number5,.number6,.number7 {
              width: 12%;
              height: 5%;
              position: absolute;
            }
            .number1 {
              left: 4%;
              top: 45%;
            }
            .number2 {
              left: 8.9%;
              top: 64%;
            }
            .number3 {
              left: 15.9%;
              top: 88%;
            }
            .number4 {
              transform: rotate(90deg);
              left: 41.5%;
              top: 72%;
            }
            .number5 {
              transform: rotate(90deg);
              left: 53.5%;
              top: 72%;
            }
            .number6 {
              transform: rotate(90deg);
              left: 66.5%;
              top: 58%;
            }
            .number7 {
              transform: rotate(90deg);
              left: 86%;
              top: 44%;
            }
          }
        }

      }
      .bottom {
        height: 3%;
        background: url("../../static/bottom.jpg") no-repeat;
        background-size: 100% 100%;
      }
    }

</style>



